
#networkpage {
	display: grid;
    grid-template-columns: 200px 800px;
	grid-column-gap: 20px;
	grid-template-rows: 50px 800px;
}

.sidebar {
	grid-column:1/1;
	grid-row: 2/2;
}


.importbar {
	grid-column:1/3;
	grid-row: 1/1;
}

#SVGContainer {
	grid-column:2/3;
	grid-row: 1;
	margin-top: 50px;
}

.modal_pathway {
	cursor: pointer;
	background-color: #f0f0f0;
	margin-top: 3px;
}

.modal_pathway:hover {
	background-color: #fafaaa;
}

.Modal_ReviewItems {
	cursor: pointer;
	background-color: #e5f2ed;
	margin-top: 3px;
	height: 25px;
}

.Modal_ReviewItems:hover {
	background-color: #fafaaa;
	height: 100px;
	transition-delay: .35s;
    transition: all ease-in-out .35s;
    overflow:hidden;
}
