

.wrapper {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 20px 0;
    transition: all 0.5s ease;
	display: grid;
    grid-template-columns: 200px 700px;
	grid-column-gap: 20px;
	grid-template-rows: 800px;
}

.container {
	height: 100%;
}

.home-body {
	grid-column:2/4;
	grid-row: 1;
}