

.sidebar {
	height: 95%;
	overflow-x: hidden;
	width: 200px;
}

.menu-item:hover {
	background-color: #888;
	transition: 1s;
	transition-delay: .5s;
	color: red;
}

.box {
	
	--main-color: #efe7ce;
	
    position:relative;
    background:linear-gradient(to right, var(--main-color) , var(--main-color)) top left,
    linear-gradient(to right, var(--main-color) , var(--main-color)) bottom right,
    linear-gradient(to right, var(--main-color) , var(--main-color)) top left,
    linear-gradient(to right, var(--main-color) , var(--main-color)) bottom right;
	background-repeat:no-repeat;
    background-size:3px 0%, 3px 0%, 0% 3px, 0% 3px;
    transition:500ms ease-out;
}

.box:hover {
   background-size:3px 100%, 3px 100%, 100% 3px, 100% 3px;
}


.modalPopup {
	height: 600px;
	width: 100%;
}